import React from 'react';
import './card.css';

function Card(props)
{
    return(
        <div className="card-div">
            <div className="card-part-1">
                <img src={props.image} alt={props.topic} className="card-image"/>
                <h4>{props.topic}</h4>
            </div>
            <div className="card-part-2">
                <p className="card-details">{props.details}</p>
                <form><input type="submit" value="Learn More" class="learn-more"></input></form>
            </div>
        </div>
    )
}

export default Card;