import React from 'react';
import './content.css'
import Card from './card.jsx';
import './card.css';

var lnk = "data:image/png;base64,";
var topics = 
[
    {
        topic:"Why only wolves?", image:lnk, details:"We love all animals, but we felt that we were not doing enough for wolves. So we took the initiative to help them out."
    },
    {
        topic:"How do we do this?", image:lnk, details:"We are lit"
    },
    {
        topic:"How do we keep track?", image:lnk, details:"We are lit"
    },
    {
        topic:"Learn about Wolves", image:lnk, details:"We are lit"
    },
    {
        topic:"Important Trivia", image:lnk, details:"We are lit"
    },
    {
        topic:"Take a pledge", image:lnk, details:"We are lit"
    },
]

function Content()
{
    return(
        <div className="content-div">
            {
                topics.map(function card(i)
                {
                    return <Card topic={i.topic} image={i.image} details={i.details}/>
                })
            }
        </div>
    )
}

export default Content;